function _weekdaysLangs(language) {
  switch (language) {
    case 'en':
      return [
        'Sunday',
        'Monday',
        'Tuesday',
        'Wednesday',
        'Thursday',
        'Friday',
        'Saturday'
      ]
    case 'ar':
      return [
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ]
    case 'fr':
      return [
        'Dimanche',
        'Lundi',
        'Mardi',
        'Mercredi',
        'Jeudi',
        'Vendredi',
        'Samedi'
      ]
    default:
      throw new Error('Language mapping error in _weekdayNames with language=', language)
  }
}

function _aqiLangs(language) {
  switch (language) {
    case 'en':
      return {
        1: 'Air Quality: Good',
        2: 'Air Quality: Fair',
        3: 'Air Quality: Moderate',
        4: 'Air Quality: Poor',
        5: 'Air Quality: Very Poor'
      }
    case 'ar':
      return {
        1: ' : ',
        2: ' : ',
        3: ' : ',
        4: ' : ',
        5: ' :  '
      }
    case 'fr':
      return {
        1: 'Qualit de l\'air: bonne',
        2: 'Qualit de l\'air: passable',
        3: 'Qualit de l\'air: modre',
        4: 'Qualit de l\'air: mdiocre',
        5: 'Qualit de l\'air: trs mauvaise'
      }
    default:
      throw new Error('Language mapping error in _aqiInterpretation with language=', language)
  }
}

function _picturesLangs(language) {
  let success, fail
  switch (language) {
    case 'en':
      success = (place) => `Local pictures of ${place}`
      fail = (place) => `There are no pictures found for ${place}`
      return [success, fail]
    case 'ar':
      success = (place) => `  ${place}`
      fail = (place) => `    ${place}`
      return [success, fail]
    case 'fr':
      success = (place) => `Photos locales de ${place}`
      fail = (place) => `Il n'y a pas de photos trouves pour ${place}`
      return [success, fail]
    default:
      throw new Error('Language mapping error in _picturesLangs with language=', language)
  }
}
